* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.fullBodyBG {
  background: linear-gradient(#04619f, #04619f);
  background: -webkit-linear-gradient( #04619f, #004574);
  /* background: linear-gradient(#000428 , #002d552f), url('../imgs/bg2.png') no-repeat center center fixed ; */
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100vh;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.main-box {
  /* min-height: 80vh; */
  /* overflow: hidden; */
  width: 90%;
  /* background: linear-gradient(to right bottom, #8c07c596, #780fa596); */
  /* border-radius: 3rem; */
  /* backdrop-filter: blur(3rem); */
}

.glassBg-box {
  background: linear-gradient(to right bottom, #0f88d8c7, #0583d685);
  border-radius: 2rem;
  backdrop-filter: blur(3rem);
}



.text-bg-glass{
  background: linear-gradient(to right bottom, #002741, #004574);
  border-radius: 2rem;
  backdrop-filter: blur(3rem);
  margin-left: 30%;
  margin-right: 30%;
}

.fade-text-color{
  color: #0082d8;

}

.letter-spaceing{
  letter-spacing: 10px;
}



.glow-div {
  transition: 0.2s;
  cursor: pointer;
}
.glow-div:hover {
  transform: scale(1.04);
  transition: all 0.3s ease-in-out;
}

.glow-div2 {
  transition: 0.2s;
  cursor: pointer;
}
.glow-div2:hover {
  transform: scale(1.04);
  transition: all 0.2s ease-in-out;
  box-shadow: 0px 0px 6px 0px #0082d8;

}

